import { createSlice } from '@reduxjs/toolkit';

const initialConfigurationState = { buildingId: '',
  apiUrl: '',
  pmc: '',
  screen: 'floorplans'
};

export const configurationSlice = createSlice({
  name: 'configuration',
  initialState: initialConfigurationState,
  reducers: {
    setConfiguration(state, action) {
      state.buildingId = action.payload.configuration.buildingId;
      state.apiUrl = action.payload.configuration.apiUrl;
      state.pmc = action.payload.configuration.pmc;
    },
    resetConfiguration(state) {
      state = initialConfigurationState;
    },
    setBuildingId(state, action) {
      state.buildingId = action.payload;
    },
    setPmc(state, action) {
      state.pmc = action.payload;
    },
    setApi(state, action) {
      state.apiUrl = action.payload;
    },
    setScreen(state, action) {
      console.log(action.payload);
      state.screen = action.payload;
    }
  },
});

export const { setConfiguration,
  resetConfiguration,
  setBuildingId,
  setPmc,
  setApi,
  setScreen,
} = configurationSlice.actions;

export default configurationSlice.reducer;
