import { coreApi } from "../../config/api";

const apiData = {
  urlUnits: "unit/search",
  urlUnitPricing: "unit_pricing/search",
};
  let page = 1;
  let recordPerPage = 50;
  let buildingId = "1";

export const loadUnits = async (body) => {
  try {
    let {data} = await coreApi.post(`${apiData.urlUnits}/${buildingId}?withPriceRange=true&page=${page}&recordsPerPage=${recordPerPage}`,body);
    const {records} = data
    return {records};
  } catch (error) {
    if (error.response) {
      throw new Error(error.response);
    } else {
      throw new Error("API ERROR" + error);
    }
  }
};

export const loadUnitPricing = async (body) => {
    try {
      let {data} = await coreApi.post(`${apiData.urlUnitPricing}/${buildingId}?page=${page}&recordsPerPage=${recordPerPage}`,body);
      const {records} = data
      return {records};
    } catch (error) {
      if (error.response) {
        throw new Error(error.response);
      } else {
        throw new Error("API ERROR" + error);
      }
    }
  };
